import { Directive, OnInit, Renderer2, Input, ElementRef, EventEmitter, Output } from "@angular/core";

export type SortColumn = '';
export type SortDirection = 'asc' | 'desc' | '';

const rotate: { [key: string]: SortDirection } = {
  asc: 'desc',
  desc: '',
  '': 'asc',
};

export const compare = (
  v1: string | number | boolean | Date,
  v2: string | number | boolean | Date
) => (v1 < v2 ? -1 : v1 > v2 ? 1 : 0);

export interface SortEvent {
  column: SortColumn;
  direction: SortDirection;
}

@Directive({
  selector: 'span[sortable]',
  standalone: true,
  host: {
    '[class.asc]': 'direction === "asc"',
    '[class.desc]': 'direction === "desc"',
    '(click)': 'rotate()',
  },
})
export class SortableHeaderDirective implements OnInit{
  @Input() sortable: SortColumn = '';
  @Input() direction: SortDirection = '';
  @Input() sorting: any;
  @Output() sort = new EventEmitter<SortEvent>();

  private column: HTMLElement;
  constructor(private renderer: Renderer2, private el: ElementRef) {
    this.column = this.el.nativeElement;
  }

  ngOnInit() {
  }

  rotate() {
    if(this.sorting == 'true'){
      this.direction = rotate[this.direction];
      this.sort.emit({ column: this.sortable, direction: this.direction });
    }
  }
}